import DefaultList from "@/components/content/defaultlist/DefaultList.vue"

import { mapMutations } from 'vuex'


export default {
	components: {
		DefaultList
	},
	methods: {
		...mapMutations(['logout']),
		toLogin() {
			uni.showModal({
				title: '提示',
				content: '您确定要退出当前帐户?',
				success: res => {
					if(res.confirm) {
						
						//清除全局userInfo，改变hasLogin为false(未登录状态)
						//清除本地仓储userInfo
						this.logout()
						
						uni.reLaunch({
							url: '/components/content/login/login'
						})
					}else if(res.cancel) {
						return false
					}
				},
				fail: err => {
					console.log(err)
				}
				
			})
			
		}
	}
}
